<template>
    <yd-layout>
        <div class="myPerDetail">
            <div class="mpTop">{{detail.KpiName}}</div>
            <div class="onePart">
                <div class="op_title">
                    <yd-flexbox>
                        <yd-flexbox-item>任务绩效</yd-flexbox-item>
                        <div class="jt_div" @click="jtFlag=!jtFlag;slide($refs.op_bottom.className)">
                            <img src="../../../static/images/performance/jt1@3x.png" class="jt_img" :class="{jt_img_click:jtFlag==true}">
                        </div>
                    </yd-flexbox>
                </div>
                <div class="op_bottom" ref="op_bottom" solt="op_bottom" v-if="months==1">
                    <div class="theText" v-for="(item, index) in detail.ItemList" :key="index">
                        <div class="title">{{index+1}}.工作任务：</div>
                        <div class="details" v-html="item.PlanedWork"></div>
                        <div class="iw_title">完成情况：</div>
                        <div class="infoDetail" v-html="item.ActualWork"></div>
                    </div>
                </div>
                <div class="op_bottom" ref="op_bottom" solt="op_bottom" v-if="months==0">
                    <div class="kpi" v-if="detail.ItemListKpi.length!=0">KPI（关键绩效指标）</div>
                    <div class="theText" v-for="(item, index) in detail.ItemListKpi" :key="index">
                        <div class="title">{{index+1}}.工作任务：<span class="span_weight">权重：{{item.Weight}}</span></div>
                        <div class="details" v-html="item.IndicatorName"></div>
                        <div class="iw_title">完成情况：</div>
                        <div class="infoDetail" v-html="item.Completion"></div>
                    </div>
                    <div class="gs" v-if="detail.ItemListGs.length!=0">GS（工作任务指标）</div>
                    <div class="theText" v-for="(item, index) in detail.ItemListGs" :key="'gs'+index">
                        <div class="title">{{index+1}}.工作任务：<span class="span_weight">权重：{{item.Weight}}</span></div>
                        <div class="details" v-html="item.IndicatorName"></div>
                        <div class="iw_title">完成情况：</div>
                        <div class="infoDetail" v-html="item.Completion"></div>
                    </div>
                </div>
            </div>
            <div class="onePart">
                <div class="op_title">
                    <yd-flexbox>
                        <yd-flexbox-item>考评结果</yd-flexbox-item>
                        <div class="jt_div" @click="jtFlag2=!jtFlag2;slide($refs.op_bottom2.className)">
                            <img src="../../../static/images/performance/jt1@3x.png" class="jt_img" :class="{jt_img_click:jtFlag2==true}">
                        </div>
                    </yd-flexbox>
                </div>
                <div class="op_bottom2" ref="op_bottom2" v-show="detail.Score!=null">
                    <div class="theText">
                        <div class="details">{{detail.Score}}</div>
                    </div>
                </div>
            </div>
            <div class="onePart">
                <div class="op_title">
                    <yd-flexbox>
                        <yd-flexbox-item>评价意见</yd-flexbox-item>
                        <div class="jt_div" @click="jtFlag3=!jtFlag3;slide($refs.op_bottom3.className)">
                            <img src="../../../static/images/performance/jt1@3x.png" class="jt_img" :class="{jt_img_click:jtFlag3==true}">
                        </div>
                    </yd-flexbox>
                </div>
                <div class="op_bottom3" ref="op_bottom3">
                    <div class="theText" v-if="leaderComment!=null">
                        <div class="title">直接上级：</div>
                        <div class="details" v-html="leaderComment"></div>
                    </div>
                    <div class="theText" v-if="seniorComment!=null">
                        <div class="title">间接上级：</div>
                        <div class="details" v-html="seniorComment"></div>
                    </div>
                </div>
            </div>
        </div>
        <yd-popup v-model="reasonShow" position="center" width="5.79rem" height="auto" v-show="reasonShow==true">
            <div class="other otherSeal">拒绝原因</div>
            <yd-cell-group>
                <yd-cell-item>
                    <yd-textarea id="js_otherInput" slot="right" placeholder="请输入您的拒绝原因..." v-model="rejectReason"></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes sealYes" id="js_determineBtn" @click="KpiResultConfirm()">确定</li>
                    <li class="no sealNo" id="js_cancelBtn" @click="reasonShow=false">取消</li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar class="bot_perf" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999" v-if="confirm==0">
            <yd-tabbar-item link="/performanceList" title="待办事项">
                <yd-icon name="grdb2" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="我的考评" link="" active>
                <yd-icon name="wdkp2" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="confirm==1">
            <div class="agree" @click="confirmResult=true;KpiResultConfirm()">确认</div>
            <div class="back" @click="confirmResult=false;reasonShow=true">拒绝</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import $ from '../../jquery-vendor';
export default {
    data() {
        return {
            jtFlag: true,
            jtFlag2: false,
            jtFlag3: false,
            detail: {},
            show: false,
            leaderComment: '',
            seniorComment: '',
            confirm: this.$route.params.confirm,
            months: this.$route.params.months,
            resultId: this.$route.params.resultId,
            confirmResult: true,
            rejectReason: '',
            reasonShow: false
        }
    },
    mounted() {
        document.setTitle('我的考评');
        let vm = this;
        if (vm.$route.params.confirm == 0) {
            if (vm.months == 1) {
                vm.detailData();
            } else {
                vm.yearDetail();
            }
        } else if (vm.$route.params.confirm == 1) {
            vm.GetKpiResultConfirmContent();
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        // 月度季度年度详情页接口
        GetKpiResultConfirmContent() {
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/KpiResult/GetKpiResultConfirmContent', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                ResultId: vm.$route.params.resultId,
                KpiId: vm.$route.params.id,
                Type: vm.months
            }, function(res) {
                console.log(JSON.parse(res.JsonData));
                vm.detail = JSON.parse(res.JsonData);
                vm.leaderComment = vm.detail.LeaderScore.Comment;
                vm.seniorComment = vm.detail.SeniorLeaderScore.Comment;
            }, function(res) {
                if (res.response.data.IsSuccess == false) {
                    vm.$dialog.toast({
                        mes: '该流程已处理',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/performanceList');
                        }
                    });
                }
            });
        },
        // 月度绩效详情接口
        detailData() {
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/MonthlyKpi/GetSingleMonthlyKpi', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                Id: vm.$route.params.id
            }, function(res) {
                console.log(JSON.parse(res.JsonData));
                vm.detail = JSON.parse(res.JsonData);
                vm.leaderComment = vm.detail.LeaderScore.Comment;
                vm.seniorComment = vm.detail.SeniorLeaderScore.Comment;
                // vm.ItemList = vm.detail.ItemList;
            });
        },
        slide(className) {
            $('.' + className).slideToggle();
        },
        // 确认拒绝接口
        KpiResultConfirm() {
            let vm = this;
            if (vm.confirmResult == false && vm.rejectReason == '') {
                vm.$dialog.toast({
                    mes: '请输入您的拒绝原因',
                    timeout: 2000
                });
            } else {
                vm.reasonShow == false;
                vm.post(vm.conf.portalMobile + '/API/HRMSApi/api/KpiResult/KpiResultConfirm', {
                    token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    ResultId: vm.$route.params.resultId,
                    ConfirmResult: vm.confirmResult,
                    RejectReason: vm.rejectReason
                }, function(res) {
                    console.log(res);
                    if (res.IsSuccess == false || res.IsSuccess == 'false') {
                        vm.$dialog.toast({
                            mes: res.ErrorMessage,
                            timeout: 2000
                        });
                    } else if (res.IsSuccess == true || res.IsSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '提交成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/performanceList');
                            }
                        });
                    }
                });
            }
        },
        // 年度绩效详情接口
        yearDetail() {
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/YearlyKpi/GetSingleYearlyKpi', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                Id: vm.$route.params.id
            }, function(res) {
                console.log(JSON.parse(res.JsonData));
                vm.detail = JSON.parse(res.JsonData);
                vm.leaderComment = vm.detail.LeaderScore.Comment;
                vm.seniorComment = vm.detail.SeniorLeaderScore.Comment;
            });
        }
    }
}
</script>
<style lang="less" scoped>
@import './performance.less';

.yd-icon-grdb2:before,
.yd-icon-wdkp2:before {
    display: block;
    content: "";
}

.yd-icon-grdb2:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/performance/grdb2@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
}

.yd-icon-wdkp2:before {
    width: 0.35rem;
    height: 0.35rem;
    background: url(../../../static/images/performance/wdkp2.png) no-repeat 0 0;
    background-size: 0.35rem;
}

.theText {
    padding-bottom: .2rem;
}

.iw_title {
    font-size: .3rem;
    color: #333;
    font-weight: bold;
    padding-bottom: .15rem;
    padding-top: .2rem;
}

.agree,
.back {
    width: 50%;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
}

.agree {
    background-color: #59a5fd;
}

.back {
    background-color: #ff536f;
}

.otherSeal {
    font-size: 0.32rem;
    font-weight: bold;
}

.yd-popup-center {
    border-radius: 5px;
}

.kpi {
    color: #333;
    font-weight: bold;
    font-size: .3rem;
    padding-left: .44rem;
    line-height: 1rem;
    border-bottom: 1px solid #eaeaea;
}

.gs {
    color: #333;
    font-weight: bold;
    font-size: .3rem;
    padding-left: .44rem;
    line-height: 1rem;
    border-top: .2rem solid #f8f8f8;
    border-bottom: 1px solid #eaeaea;
}

.span_weight {
    float: right;
}
</style>


